// tslint:disable-next-line:no-any variable-name
type AnyFunctionType = (...args: any[]) => any;

interface IPubSub {
    // tslint:disable-next-line:no-any
    publish: (...args: any[]) => IPubSub;
    subscribe: (subscriber: AnyFunctionType) => string;
    unsubscribe: (id: string) => IPubSub;
}

export class PubSub implements IPubSub {
    private pool = {};
    private count: number = 0;

    /**
     * publish - method to publish a message to the channel.
     *
     * @public
     * @param {Any} args Arguments.
     * @return {Object} `this` instance.
     */
    // tslint:disable-next-line:no-any
    public publish = (...args: any[]): IPubSub => {
        for (let item of Object.keys(this.pool)) {
            this.pool[item](...args);
        }

        return this;
    };

    /**
     * subscribe - method to subscribe to the channel.
     *
     * @public
     * @param {Function} subscriber Subscriber function.
     * @return {String} Subscriber `id` token.
     */
    public subscribe = (subscriber: AnyFunctionType): string => {
        this.count++;
        const id = `${this.count}`;
        this.pool[id] = subscriber;

        return id;
    };

    /**
     * unsubscribe - method to unsubscribe from the channel.
     *
     * @public
     * @param {String} id Subscriber `id`.
     * @return {Object} `this` instance.
     */
    public unsubscribe = (id: string): IPubSub => {
        delete this.pool[id];

        return this;
    };
}



