import React from 'react'; // tslint:disable-line:no-unused-variable
import classNames from 'classnames';

import styles from './Dropdown.scss';

export interface IDropdownOption {
    value: string;
    append?: string;
    disabled?: boolean;
}

// tslint:disable:variable-name
const Option = ({ option }) => {
    const { value, disabled, append = '' }: IDropdownOption = option;

    return (
        <option value={value} disabled={disabled}>
            {`${value}${append}`}
        </option>
    );
};

const Dropdown = props => {
    // tslint:disable:variable-name
    const { options, onChange, selected, className } = props;

    return (
        <div className={classNames(styles.dropdown, className)} tabIndex={-1}>
            <select className={styles.select} onChange={onChange} value={selected} title={selected}>
                {options.map((option, key) => {
                    return <Option option={option} key={key} />;
                })}
            </select>
            <div className={styles.header}> {selected} </div>
            <div className={styles.headerPlaceHolder}>{selected}</div>
            <div className={styles.arrow} />
        </div>
    );
};

export { Dropdown };



